/* HEAD */


.HEAD{
    width: 100%;
	height: 100vh;
	display: flex;
	flex-direction: column;
	background: #eff0f2;
	position: relative;
}

.HEAD__container{
	position: fixed;
	width: 100%;
	max-width: 1920px;
	z-index: 999;
	background: #eff0f2;
}


.HEAD__container_headline{
	display: flex;
	justify-content: space-between;
	align-items: center;
    width: 80%;
    height: 12vh;
    margin: 0 auto;

}



.HEAD__container_headline_logo{
	display: flex;
	align-items: flex-end;
	
	a{
		border: 5px solid black;
		padding: 4%;

		img{
			max-width: 100%;
		}
	}
}



.HEAD__container_headline_touch-menu{
	position: relative;
	display: block;

	 &.active {
       

        .HEAD__container_headline_touch-menu_icon {
            justify-content: flex-start;
            span {
                &:first-child {
                    transform: rotate(45deg);
                    position: relative;
					top: 9px;
					transition-duration: .3s;
					transition-property: transform;
					transition-timing-function: ease-in-out;
                }

                &:last-child {
                    transform: rotate(-45deg);
                    position: relative;
					top: 2px;
					transition-duration: .3s;
					transition-property: transform;
					transition-timing-function: ease-in-out;
                }

                &:nth-of-type(2) {
                    opacity: 0;

                }
            }
        }
    }
}


.HEAD__container_headline_touch-menu_icon{
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	width: 40px;
	height: 30px;

	span{
		width: 100%;
		height: 4px;
		background: #606061;
		@include radius(2px);
		transition-duration: .3s;
		transition-property: transform;
		transition-timing-function: ease-in-out;
	}

}

.HEAD__container_headline_touch-menu_dropdown{
	position: absolute;
	display: flex;
	width: 800px;
	left: -2200%;
	top: 3%;
	transition-duration: 1s;
	transition-property: width;
	transition-timing-function: ease-in-out;

	&.toggle {
		width: 0;
		padding: 0;
		transition-duration: 1s;
		transition-property: width;
		transition-timing-function: ease-in-out;
		
	}
}


.HEAD__container_headline_touch-menu_dropdown_list{
	overflow: hidden;
	padding: .5% 1%;
	
	a{
		font-size: 14px;
		font-family: 'Montserrat', sans-serif;
		color: #606061;
		font-weight: bold;
		text-transform: uppercase;
		white-space: nowrap;
		

		&:hover{
			border-bottom: 2px solid #606061;
		}
	}
}

.HEAD_content{
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	text-align: center;

	.HEAD_content_translate-right{
		margin-top: -7%;
	}

	h1{
		font-family: 'Montserrat', sans-serif;
		margin-top: 10%;
		font-weight: 900;
		font-size: 45px;
		font-weight: bolder;
		text-transform: uppercase;
		color: #505050;
		text-shadow: 8px 8px 3px rgba(80, 80, 80, .2);
		line-height: 1.5;
		white-space: nowrap;

		span{
			background: #b2f2ee;
			padding: 0 1%;
		}
	}
}

.HEAD_content_bottom-container{
	width: 100%;
	height: 50%;
	display: flex;
	align-items: center;
	justify-content: space-around;
	position: relative;
	 

	.HEAD_content_bottom-container_btn{
		border: 3px solid #777879;
		color:#3c3c3d;
		font-family: 'Montserrat', sans-serif;
		font-size: 18px;
		letter-spacing: 2px;
		font-weight: 600;
		text-transform: uppercase;
		padding: 1.3% 4.5%;
	}

	.HEAD_content_bottom-container_img{
		position: absolute;
		right: 10%;
		top: 50%;
	}


}



