/* SMALL_TEAM */


.SMALL_TEAM{
	width: 90%;
	margin: 0 auto;
	text-align: center;
	margin-bottom: 15%;
}

.SMALL_TEAM_container{
	margin: 0;
	padding: 0;
	display: block;
	width: 100%;
}

.SMALL_TEAM_container_img{
	display: inline-block;
	position: relative;
	width: 30%;
	padding-bottom: 34%;
	padding-top: 30px;
	height: 0;
	overflow: hidden;


	&:hover .SMALL_TEAM_container_img_tooltip{
		@include transition(all .7s);
		opacity: 1;
	}

	&:active .SMALL_TEAM_container_img_tooltip{
		@include transition(all .7s);
		opacity: 1;
	}
	
	.SMALL_TEAM_container_img_tooltip{
		top: 41%;
		left: 6.5%;
		position: absolute;
		width: 90%;
		height: 55%;
		background: #6df3e9;
		z-index: 2;
		padding: 10%;
		text-align: left;
		opacity: 0;
		cursor: pointer;

		&::after {
			content: ''; 
			position: absolute;
			top: -30px;
			left: 20%; /* Положение треугольника */
