/* BRANDS */


.BRANDS{
	display: flex;
	flex-direction: column;
	width: 80%;
	margin: 5% auto 0 auto;
	text-align: center;
	position: relative;
	align-items: center;
	justify-content: space-between;

	a{
		background: url("../img/2-copy-2.png") center no-repeat;
		display: block;
		position: relative;
		width: 140px;
		height: 63px;

		&:hover{
			background: url("../img/1-copy-2.png") center no-repeat;
		}

		&:active{
			background: url("../img/1-copy-2.png") center no-repeat;
	 	}
	}
	
}

.BRANDS__top-icon{
	display: flex;
	width: 60%;
	justify-content: space-between;
	margin-bottom: 3%;
	

	a{
		
		&:nth-of-type(1){
			background-position: 31.9% 0;
		}

		&:nth-of-type(2){
			background-position: 60.5% 93%;
		}

		&:nth-of-type(3){
			background-position: 73.3% 0;
		}
	}
}


.BRANDS__bottom-icon{
	display: flex;
	width: 100%;
	justify-content: space-around;

	.BRANDS__bottom-icon_left-box{
		width: 50%;
		display: flex;
		justify-content: space-around;

		a{	

			&:nth-of-type(1){
				background-position: -.5% 93%;
			}

			&:nth-of-type(2){
				background-position: 20% 90%;
			}

			&:nth-of-type(3){
				background-position: 40% 90%;
			}
		}

	}

	.BRANDS__bottom-icon_right-box{
		width: 50%;
		display: flex;
		justify-content: space-around;

		a{	

			&:nth-of-type(1){
				background-position: 60.5% 93%;
			}
	
			&:nth-of-type(2){
				background-position: 73.3% 0;
			}
	
			&:nth-of-type(3){
				background-position: 31.9% 0;
			}
		}
	}
}


