/* FOOTER */
.FOOTER {
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  background: #262b2e;
  padding: 7%; }
  .FOOTER h5 {
    color: #6eeee4;
    font-size: 23px;
    font-family: 'Raleway', sans-serif;
    text-transform: uppercase;
    font-size: medium 900 !important;
    cursor: pointer; }
  .FOOTER p {
    font-size: medium 900 !important;
    color: #bbb8b9;
    font-size: 16px;
    font-family: 'Raleway', sans-serif;
    cursor: pointer; }

.FOOTER__container {
  margin: 0 auto;
  display: flex; }

.FOOTER__container_icons {
  width: 40px;
  height: 40px;
  background: #424648;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 20px;
  color: #bbb8b9;
  margin-top: 10%;
  margin-bottom: 15%; }

.FOOTER__container_icons:hover {
  background: #6eeee4;
  color: #070e0d;
  -webkit-transition: all ease-in-out 0.3s;
  -moz-transition: all ease-in-out 0.3s;
  -ms-transition: all ease-in-out 0.3s;
  -o-transition: all ease-in-out 0.3s;
  transition: all ease-in-out 0.3s; }

@media screen and (max-width: 767px) {
  .FOOTER p {
    font-size: 12px; } }
